<script>
import { Offcanvas } from 'bootstrap';
import { apiGetOrder } from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      tempData: {},
    };
  },
  methods: {
    showModal() {
      this.modal.show();
    },
    getOrder(id) {
      apiGetOrder(id)
        .then((res) => {
          console.log(res.data.temporaryWorkers);
          this.tempData = res.data;
          this.showModal();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.modal = new Offcanvas(this.$refs.demoOffcans);
    this.$emitter.on('openCall', (e) => {
      this.getOrder(e);
    });
  },
};
</script>
